export default {
	data() {
		return {
			tempFace:""
		}
	},
	
	onLoad(params) {
		var tempFilePath = params.tempFilePath;
		this.tempFace = tempFilePath;
	},
	methods: {
		//重新上传头像
		changePendingFace(){
			var _this = this;
			uni.chooseImage({
				count: 1,
				sizeType: ["compressed"],
				sourceType: ["album"],
				success(res) {
					//获取临时路径
					var tempFilePath = res.tempFilePaths[0];
					_this.tempFace = tempFilePath;
				}
			})
		},
		
		upload(){
			var _this = this;
			var globalUser = _this.getGlobalUser("globalUser");
			uni.showLoading({
				mask:true,
				title:"上传中,稍等哦~~~"
			});
			
			uni.uploadFile({
				url:_this.serverUrl+'/user/uploadFace?userId='+globalUser.id,
				filePath:_this.tempFace,
				name:"file",
				//加上header头
				header:{
					"headerUserId":globalUser.id,
					"headerUserToken":globalUser.userUniqueToken
				},
				success(res) {
					console.log(res);
					var resDataStr = res.data;
					//把字符串转为对象
					var resData = JSON.parse(resDataStr);
					if(resData.status ==200){
						//获得最新的用户数据
						var userInfo = resData.data;
						uni.setStorageSync("globalUser",userInfo);
						uni.navigateBack({
							delta: 1
						})
					}else if(resData.status == 502 || resData.status == 500){
						uni.showToast({
							title:res.data.msg,
							image:"",
							duration:2000
						})
					}
					
					
				},
				complete() {
					uni.hideLoading();
				}
			})
		}
	}
}
